@use './styles/partials/colors';
@use './styles/partials/mixins';
@use './styles/partials/checkbox';


*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Avenir Next LT';
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
